@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/_mixins' as mixin;

.news-page {
  padding-bottom: 88px;

  @include mixin.small-tablet {
    padding-bottom: 64px;
  }

  &__title {
    text-align: center;
  }

  &__content {
    max-width: 832px;
    margin: 0 auto;

    img {
      max-width: 100%;

      &:first-child {
        margin-bottom: 48px;
      }
    }

    p {
      margin-bottom: 40px;
    }

    h3 {
      color: var.$secondary;
      font-family: var.$font-family;
      font-size: 2.4rem;
      font-weight: 700;
      line-height: 1.66;
      margin-top: 48px;
      margin-bottom: 24px;
    }

    ul {
      list-style: disc;
      margin-bottom: 24px;
      padding-left: 20px;

      li {
        color: var.$secondary;
        font-family: var.$font-family;
        font-size: 1.8rem;
        font-style: normal;
        font-weight: 700;
        line-height: 1.66;
      }
    }

    a[download] {
      display: block;
      color: var.$gray;
      font-family: var.$font-family;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.5;
      margin-top: 16px;

      span:last-child {
        color: var.$primary;
        margin-left: 16px;
      }
    }
  }

  &__iframe {
    width: 100%;
    aspect-ratio: 1;
  }
}

.news-id-page {
  padding-bottom: 151.58px;

  &__title {
    @include mixin.small-tablet {
      font-size: 3.2rem;
      text-align: center;
    }
  }
}

.news {
  &__settings {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 28px;
    margin-bottom: 48px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }
  }

  &__tabs {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 12px;
  }

  &__tab {
    padding: 8px 24px !important;
    font-family: var.$font-family;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.5;
    color: var.$secondary;

    &:hover {
      background: var.$primary;
      color: var.$light;
    }

    &.badge--orange {
      color: var.$light;
    }
  }

  &__list {
    margin-bottom: 48px;
    gap: 20px 0;
    margin-left: -10px;
    margin-right: -10px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
      gap: 16px 0;
    }

    &>div {
      padding: 0 10px;
    }
  }
}

.news-filter {
  color: var.$gray2;

  &__dropdown-btn {
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 8px 16px;

    span {
      color: var.$gray2;
      font-family: var.$font-family;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.5;
      letter-spacing: 0.5px;
    }

    svg {
      width: 16px;
      fill: var.$secondary;
    }
  }

  &__list {
    a {
      white-space: nowrap;
    }
  }
}

.news-item {
  width: 100%;
  height: 100%;
  padding: 0;
  min-height: 400px;
  display: flex;
  align-items: stretch;
  overflow: hidden;
  transition: 0.3s linear;
  box-sizing: border-box;

  @include mixin.small-tablet {
    min-height: unset;
  }

  &:hover {
    background: var.$secondary;
  }

  &>.col-12,
  &>.col-lg-6 {
    padding: 0;
  }

  &__img {
    overflow: hidden;
    flex-basis: 50%;

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  &__content {
    height: 100%;
    padding: 32px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  
  &__head {
    width: 100%;
  }

  &__tags,
  &__files,
  &__head {
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    gap: 8px;
  }

  &__files {
    flex-grow: 1;
  }

  &__type {
    font-size: 1.6rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &:hover &__type {
    color: #fff;
  }

  &__head {
    width: 100%;
    margin-bottom: 24px;
  }

  &__tag,
  &__file {
    color: var.$light;
    font-family: var.$font-family2;
    font-size: 1.4rem;
    font-weight: 500;
    line-height: 1.43;
    letter-spacing: -0.1px;
  }

  &:hover &__file {
    background: #EDF1F4;
    color: var.$secondary;
  }

  &__date {
    color: var.$gray;
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 1.66;
    margin-bottom: 16px;
  }

  &__title {
    color: var.$secondary;
    flex-grow: 1;
    width: 100%;
    text-align: left;
    white-space: normal;
    transition: 0.3s linear;
    margin-bottom: 32px;
    line-height: 1.66;

    span {
      max-width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      // word-break: break-all;
    }
  }

  &:hover &__title {
    color: var.$light;
  }

  &__more {
    display: flex;
    align-items: center;
    gap: 6px;
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-weight: 500;
    line-height: 1;

    svg {
      width: 20px;
    }

    &:hover {
      color: var.$secondary;
    }
  }

  &:hover &__more {
    color: var.$light;

    path {
      stroke: var.$light;
    }
  }
}

.news-share {
  max-width: 832px;
  margin: 0 auto;

  &__title {
    margin-bottom: 24px;
  }

  &__list {
    display: flex;
    align-items: center;
    gap: 24px;

    a {
      path {
        transition: 0.3s linear;
      }

      &:hover {
        path {
          fill: var.$primary;
        }
      }
    }
  }
}

.news-recom {
  margin-top: 96px;

  @include mixin.small-tablet {
    margin-top: 48px;
  }

  &__title {
    margin-bottom: 48px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }
  }

  &__body {
    gap: 20px 0;
    margin-left: -10px;
    margin-right: -10px;

    &>div {
      padding: 0 10px;
    }
  }
}